import React from "react";
import "../App.css";

export default function CovidForm(props) {
  return (
    <div className="container">
      <div className="forbWell">
        <h3>
          Fill in your details below as it pertains to your region and your
          estimate will be given below{" "}
          <span className="glyphicon glyphicon-circle-arrow-right arr"></span>
        </h3>
        <div className="forWell">
          <form onSubmit={props.onSubmission}>
            <div className="form-group text-left ">
              <label htmlFor="name">
                <h5> Region:</h5>
              </label>
              <select
                className="form-control"
                name="name"
                onChange={props.onRegionChange}
                value={props.covidData.region.name}
                required
              >
                <option value="" />
                <option value="Africa">Africa</option>
                <option value="Asia">Asia</option>
                <option value="America">America</option>
                <option value="Europe">Europe</option>
              </select>
            </div>
            <div className="form-group text-left">
              <label htmlFor="avgAge">
                {" "}
                <h5>Average Age:</h5>
              </label>
              <input
                className="form-control"
                type="number"
                name="avgAge"
                onChange={props.onRegionChange}
                value={props.covidData.region.avgAge || ""}
                required
              ></input>
            </div>
            <div className="form-group text-left">
              <label htmlFor="avgDailyIncomeInUSD">
                <h5>Average Daily Income in $:</h5>
              </label>
              <input
                className="form-control"
                type="number"
                name="avgDailyIncomeInUSD"
                onChange={props.onRegionChange}
                value={props.covidData.region.avgDailyIncomeInUSD || ""}
                required
              ></input>
            </div>
            <div className="form-group text-left">
              <label htmlFor="avgDailyIncomePopulation">
                <h5>
                  Percentage of Population that can receive the average daily
                  income:
                </h5>
              </label>
              <input
                className="form-control"
                type="number"
                name="avgDailyIncomePopulation"
                onChange={props.onRegionChange}
                value={props.covidData.region.avgDailyIncomePopulation || ""}
                required
              ></input>
            </div>
            <div className="form-group text-left">
              <label htmlFor="periodType">
                {" "}
                <h5>Period Type:</h5>{" "}
              </label>
              <select
                className="form-control"
                name="periodType"
                onChange={props.onChange}
                value={props.covidData.periodType}
                required
              >
                <option value="days">Days</option>
                <option value="weeks">Weeks</option>
                <option value="months">Months</option>
              </select>
            </div>
            <div className="form-group text-left">
              <label htmlFor="timeToElapse">
                <h5>Period in Numbers:</h5>
              </label>
              <input
                className="form-control"
                type="number"
                name="timeToElapse"
                onChange={props.onChange}
                value={props.covidData.timeToElapse || ""}
                required
              ></input>
            </div>
            <div className="form-group text-left">
              <label htmlFor="reportedCases">
                <h5>Reported Cases:</h5>
              </label>
              <input
                className="form-control"
                type="number"
                name="reportedCases"
                onChange={props.onChange}
                value={props.covidData.reportedCases || ""}
                required
              ></input>
            </div>
            <div className="form-group text-left">
              <label htmlFor="population">
                {" "}
                <h5>Population:</h5>
              </label>
              <input
                className="form-control"
                type="number"
                name="population"
                onChange={props.onChange}
                value={props.covidData.population || ""}
                required
              ></input>
            </div>
            <div className="form-group text-left">
              <label htmlFor="totalHospitalBeds">
                <h5>Total Hospital Beds:</h5>{" "}
              </label>
              <input
                className="form-control"
                type="number"
                name="totalHospitalBeds"
                onChange={props.onChange}
                value={props.covidData.totalHospitalBeds || ""}
                required
              ></input>
            </div>
            <div>
              <button class="btn btn-info btn-block" type="submit">
                <h4>
                  <strong> Estimate </strong>{" "}
                </h4>
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
