import React from "react";
import "../App.css";

export default function EstimateAns({ data, toShow }) {
  if (toShow) {
    return null;
  }
  return (
    <div className="container">
      <div className="forbWell">
        {" "}
        <h3>Moderate Estimate</h3>
        <div className="forWell">
          <div className="form-group">
            <label htmlFor="currentlyInfected">
              {" "}
              <h4>Currently Infected:</h4>
            </label>
            <input
              className="form-control"
              name="currentlyInfected"
              value={data.impact.currentlyInfected || ""}
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor=" infectionsByRequestedTime">
              <h4>No of Infections by end of the Period:</h4>{" "}
            </label>
            <input
              className="form-control"
              name=" infectionsByRequestedTime"
              value={data.impact.infectionsByRequestedTime || ""}
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="hospitalBedsByRequestedTime">
              <h4>
                No of available/non-available Hospital beds for severe patients:
              </h4>{" "}
            </label>
            <input
              className="form-control"
              name="hospitalBedsByRequestedTime"
              value={data.impact.hospitalBedsByRequestedTime || ""}
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="severeCasesByRequestedTime">
              <h4>
                Severe Cases by the end of the period that will require
                hospitilization:
              </h4>
            </label>
            <input
              className="form-control"
              name="severeCasesByRequestedTime"
              value={data.impact.severeCasesByRequestedTime || ""}
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="casesForICUByRequestedTime">
              <h4>
                Severe cases by the end of the period that will require ICU
                admission:
              </h4>
            </label>
            <input
              className="form-control"
              name="casesForICUByRequestedTime"
              value={data.impact.casesForICUByRequestedTime || ""}
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="casesForVentilatorsByRequestedTime">
              <h4>
                Severe cases by the end of the period that will require
                Ventilators:
              </h4>
            </label>
            <input
              className="form-control"
              name="casesForVentilatorsByRequestedTime"
              value={data.impact.casesForVentilatorsByRequestedTime || ""}
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="dollarsInFlight">
              <h4>How much money the economy is likely to lose in a day:</h4>
            </label>
            <input
              className="form-control"
              name="dollarsInFlight"
              value={data.impact.dollarsInFlight || ""}
              readOnly
            ></input>
          </div>
        </div>
      </div>
      <div className="forbWell">
        <h3>Worst Case Scenario Estimate</h3>

        <div className="forWell">
          <div className="form-group">
            <label htmlFor="currentlyInfected">
              <h4>Currently Infected:</h4>{" "}
            </label>
            <input
              name="currentlyInfected"
              value={data.severeImpact.currentlyInfected || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor=" infectionsByRequestedTime">
              <h4>No of Infections by end of the Period:</h4>{" "}
            </label>
            <input
              name=" infectionsByRequestedTime"
              value={data.severeImpact.infectionsByRequestedTime || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="hospitalBedsByRequestedTime">
              <h4>
                {" "}
                No of available/non-available Hospital beds for severe patients:
              </h4>{" "}
            </label>
            <input
              name="hospitalBedsByRequestedTime"
              value={data.severeImpact.hospitalBedsByRequestedTime || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="severeCasesByRequestedTime">
              <h4>
                Severe Cases by the end of the period that will require
                hospitilization:
              </h4>
            </label>
            <input
              name="severeCasesByRequestedTime"
              value={data.severeImpact.severeCasesByRequestedTime || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="casesForICUByRequestedTime">
              <h4>
                Severe cases by the end of the period that will require ICU
                admission:
              </h4>
            </label>
            <input
              name="casesForICUByRequestedTime"
              value={data.severeImpact.casesForICUByRequestedTime || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="casesForVentilatorsByRequestedTime">
              <h4>
                Severe cases by the end of the period that will require
                Ventilators:
              </h4>
            </label>
            <input
              name="casesForVentilatorsByRequestedTime"
              value={data.severeImpact.casesForVentilatorsByRequestedTime || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
          <div className="form-group">
            <label htmlFor="dollarsInFlight">
              <h4>How much money the economy is likely to lose in a day:</h4>
            </label>
            <input
              name="dollarsInFlight"
              value={data.severeImpact.dollarsInFlight || ""}
              className="form-control"
              readOnly
            ></input>
          </div>
        </div>
      </div>
    </div>
  );
}
